body {
  font-family: sans-serif;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-height: 100vh;
}

.App {
  height: 20rem;
  width: 25rem;
  box-shadow: 10px 10px 38px 0px rgba(0, 0, 0, 0.75);
}

button {
  border-radius: 100px;
  height: 80px;
  width: 10rem;
  margin-top: 5rem;
  font-size: 20px;
  color: #ffffff;
  background: rgb(105, 104, 104);
  border: 2px #ffffff solid;

}

button:hover {
  background: rgb(184, 184, 184);
  cursor: pointer;
}

button:focus {
  outline: 0;
}
